export default "/* Spariscono le input */\n::ng-deep .ngx-timepicker input {\n  visibility: collapse;\n  font-size: 0 !important;\n}\n/* The colon disappears. Find another solution */\n::ng-deep span.ngx-timepicker__time-colon.ngx-timepicker__control--second {\n  font-size: 0;\n}\n/* the clock is displayed inside the input */\n::ng-deep ngx-material-timepicker-toggle.ngx-timepicker__toggle.ng-star-inserted {\n  position: absolute;\n  left: 0;\n  margin-top: 1pc;\n  width: -webkit-fill-available;\n}\n/* View che permette l'accio del Timepicker a forma di orologio */\n::ng-deep ngx-material-timepicker-toggle {\n  border-radius: 0;\n  background-color: var(--input-disabled);\n  width: 93%;\n  margin: 2rem;\n  border: 3px solid #303030;\n  box-shadow: 1px 1px 5px 1px #303030;\n}\n/* the clock's button is as large as the input */\n::ng-deep button.ngx-material-timepicker-toggle {\n  background: blue;\n  width: 100%;\n}\n/* clock icon disappears from the button */\n::ng-deep svg.ng-star-inserted {\n  visibility: hidden;\n}\n/* the components arrow disappear */\n::ng-deep span.ngx-timepicker-control__arrow {\n  visibility: collapse;\n}\n/* button inside the time picker component is not bigger than container anymore */\n::ng-deep .ngx-material-timepicker-toggle[_ngcontent-xwl-c177] {\n  width: 100%;\n}\n/* The input of the input time picker expands to full screen  */\n#in-input-timepicker {\n  width: 100%;\n}\n/* Timepicker div text */\n#Timepicker-text {\n  position: absolute;\n  z-index: 1;\n  color: var(--grey-text);\n  /* Text can't be selected and allows to trigger timepicker */\n  pointer-events: none;\n}\n/*# sourceMappingURL=data:application/json;base64, */";